___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: #0ea5e9; /* sky-500 */
  --primary-color-darker: #0369a1; /* sky-700 */
  --bg-gradient-from: #0ea5e9;
  --bg-gradient-to: #22c55e; /* emerald-500 */
  --text-body: #0f172a; /* slate-900 */
  --text-muted: #475569; /* slate-600 */
  --border: #e2e8f0; /* slate-200 */
  --card-bg: #ffffff;
  --shadow: 0 10px 30px rgba(2, 6, 23, 0.08);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(135deg, var(--bg-gradient-from), var(--bg-gradient-to));
  background-attachment: fixed;
  font-family: 'Inter', system-ui, -apple-system, Segoe UI, Roboto, 'Helvetica Neue', Arial, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 680px;
  margin: 64px auto;
  background: var(--card-bg);
  padding: 32px 28px;
  border-radius: 16px;
  box-shadow: var(--shadow);
}

.container h1 {
  margin: 0 0 20px;
  font-size: 1.6em;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: var(--text-body);
}

form input, form label, form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

form input[type="number"] {
  font-size: 20px;
  height: 48px;
  padding: 0 16px;
  border: 1px solid var(--border);
  border-radius: 10px;
  color: var(--text-body);
}

form input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 4px rgba(14, 165, 233, 0.15);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 52px;
  cursor: pointer;
  margin-top: 16px;
  border-radius: 12px;
  transition: transform .05s ease, box-shadow .2s ease, background .2s ease;
  box-shadow: 0 8px 16px rgba(14, 165, 233, 0.25);
}

form button:hover {
  background: var(--primary-color-darker);
}
form button:active {
  transform: translateY(1px);
}

.resultado-card {
  border: 1px dashed var(--border);
  background: #f8fafc;
  border-radius: 14px;
  padding: 16px 18px;
  margin: 16px 0 24px;
}
.senha-gerada {
  font-size: 1.6em;
  color: var(--text-body);
  word-break: break-all;
}

.grupo-checks {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px 16px;
  margin: 12px 0 8px;
}
.check {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 12px;
  border: 1px solid var(--border);
  border-radius: 12px;
  background: #fff;
}
.check input[type="checkbox"] {
  width: 18px;
  height: 18px;
  accent-color: var(--primary-color);
}
.check span {
  color: var(--text-muted);
  font-size: 0.9em;
}

.campo label {
  font-weight: 600;
  color: var(--text-muted);
  margin-bottom: 6px;
}

button {
  display: block;
  margin: 24px 0 0;
  font-size: 1em;
}

@media (max-width: 520px) {
  .container {
    margin: 28px 12px;
    padding: 20px 16px;
  }
  .grupo-checks {
    grid-template-columns: 1fr;
  }
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AACA;EACE,wBAAwB,EAAE,YAAY;EACtC,+BAA+B,EAAE,YAAY;EAC7C,2BAA2B;EAC3B,yBAAyB,EAAE,gBAAgB;EAC3C,oBAAoB,EAAE,cAAc;EACpC,qBAAqB,EAAE,cAAc;EACrC,iBAAiB,EAAE,cAAc;EACjC,kBAAkB;EAClB,0CAA0C;AAC5C;;AAEA;EACE,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,SAAS;EACT,UAAU;EACV,mFAAmF;EACnF,4BAA4B;EAC5B,4KAA4K;EAC5K,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,0BAA0B;EAC1B,kBAAkB;EAClB,mBAAmB;EACnB,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;EAChB,uBAAuB;EACvB,uBAAuB;AACzB;;AAEA;EACE,cAAc;EACd,WAAW;EACX,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,eAAe;EACf,+BAA+B;EAC/B,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,kCAAkC;EAClC,8CAA8C;AAChD;;AAEA;EACE,YAAY;EACZ,gCAAgC;EAChC,WAAW;EACX,eAAe;EACf,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB,yEAAyE;EACzE,+CAA+C;AACjD;;AAEA;EACE,uCAAuC;AACzC;AACA;EACE,0BAA0B;AAC5B;;AAEA;EACE,gCAAgC;EAChC,mBAAmB;EACnB,mBAAmB;EACnB,kBAAkB;EAClB,mBAAmB;AACrB;AACA;EACE,gBAAgB;EAChB,uBAAuB;EACvB,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,gDAAgD;EAChD,cAAc;EACd,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,kBAAkB;EAClB,+BAA+B;EAC/B,mBAAmB;EACnB,gBAAgB;AAClB;AACA;EACE,WAAW;EACX,YAAY;EACZ,kCAAkC;AACpC;AACA;EACE,wBAAwB;EACxB,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,wBAAwB;EACxB,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE;IACE,iBAAiB;IACjB,kBAAkB;EACpB;EACA;IACE,0BAA0B;EAC5B;AACF","sourcesContent":["@import url('https://fonts.googleapis.com/css?family=Inter:400,600,700&display=swap');\n:root {\n  --primary-color: #0ea5e9; /* sky-500 */\n  --primary-color-darker: #0369a1; /* sky-700 */\n  --bg-gradient-from: #0ea5e9;\n  --bg-gradient-to: #22c55e; /* emerald-500 */\n  --text-body: #0f172a; /* slate-900 */\n  --text-muted: #475569; /* slate-600 */\n  --border: #e2e8f0; /* slate-200 */\n  --card-bg: #ffffff;\n  --shadow: 0 10px 30px rgba(2, 6, 23, 0.08);\n}\n\n* {\n  box-sizing: border-box;\n  outline: 0;\n}\n\nbody {